<template>
  <v-container
    fluid
    fill-height
  >
    <v-layout
      align-center
      justify-center
    >
      <v-flex
        xs12
        sm8
        md4
      >
        <v-card class="elevation-12">
          <v-toolbar
            dark
            color="primary"
          >
            <v-toolbar-title> 
              <div style="height: 36px;">
                <img
                  style="height: 100%;"
                  src="~@/assets/logoMainMono.svg"
                >
              </div>
            </v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <!-- ПОЧТА -->
              <v-text-field
                v-model="email"
                :label="$t('common.email')"
                outlined
                dense
                :error-messages="emailErrors"
                @keyup.enter="touch"
              />
              <!-- ПОЧТА КОНЕЦ -->

              <!-- ПАРОЛЬ -->
              <v-text-field
                v-model="password"
                :label="$t('common.password')"
                outlined
                dense
                :error-messages="passwordErrors"
                :type="showPass ? 'text' : 'password'"
                class="centralize-inner"
                @keyup.enter="touch"
              >
                <template #append>
                  <v-btn
                    icon
                    small
                    @click="showPass = !showPass"
                  >
                    <v-icon size="16">
                      {{ showPass ? 'far fa-eye-slash' : 'far fa-eye' }}
                    </v-icon>
                  </v-btn>
                </template>
              </v-text-field>
              <!-- ПАРОЛЬ КОНЕЦ -->
            </v-form> 
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn
              color="default"
              text
              class="mb-2"
              to="/auth/registration"
            >
              {{ $t('common.registration') }}
            </v-btn>
            <v-btn
              color="primary"
              class="mb-2"
              :loading="loading"
              @click="touch"
            >
              {{ $t('common.logIn') }}
            </v-btn>
            <!-- <router-link to="/auth/registration">
              {{ $t('common.registration') }}
            </router-link> -->
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { required, minLength, email } from 'vuelidate/lib/validators';

export default {
  name: 'Login',
    
  data () {
    return {
      colsProps: {
        cols: '12',
        md:'6',
        offsetMd:'3',
        xl:'4',
        offsetXl:'4',
      },
      showPass: false,
      email: '',
      password: '',
      touched: false,
      loading: false
    };
  },

  validations: {
    email: { required, email },
    password: { required, minLength: minLength(5) }
  },

  computed: {
    emailErrors() {
      if (this.touched && this.$v.email.$invalid) {
        return this.$t('validation.email'); 
      }
      return null;
    },
    passwordErrors() {
      if (this.touched && this.$v.password.$invalid) {
        return this.$t('validation.min', { length: 5 }); 
      }
      return null;
    }
  },

  watch: {
    email() { this.touched = false; },
    password() { this.touched = false; }
  },

  methods: {
    touch() {
      this.$v.$touch();

      if (this.$v.$invalid) {
        this.touched = true;
      } else {
        this.login();
      }
    },

    async login() {
      try {
        this.loading = true;

        const response = await this.api.post('/auth/login', {
          username: this.email,
          password: this.password
        });
        const { status, data } = response; 

        if (status === 200 && data && data.data && data.data.access_token) {
          localStorage.setItem('apiToken', data.data.access_token);
          this.api.defaults.headers.Authorization = `Bearer ${data.data.access_token}`;
          this.cloudApi.defaults.headers.Authorization = `Bearer ${data.data.access_token}`;
          this.$router.push({ path: '/tiktok/adsmanager/accounts', params: { tab: 'accounts' } });
          this.loading = false;
        } else {
          throw new Error();
        }
        
      } catch (error) {
        this.loading = false;
        console.log(error);
      }
    }
  }
};
</script>

<style>
.login-container {
  display: flex;
  align-items: center;
  height: 100%;
  padding-bottom: 28px;
}
</style>